<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="detail">
        <p class="title">{{regulationsDetailArr.title}}</p>
        <p class="prompt"><span>{{regulationsDetailArr.createdTime | substrTime}}</span><span style="float: right">浏览人数:</span></p>
        <div v-html="regulationsDetailArr.content"></div>
      </div>
    </main-part>
  </div>
</template>

<script>
  export default {
    data () {
      let vm = this
      return {
        nav: [{name: '通知公告', path: 'go(-1)'}, {name: '规章详情', path: ''}],
        id: vm.$route.query.id,
        regulationsDetailArr: []
      }
    },
    methods: {
      async regulationsDetail () {
        this.regulationsDetailArr = await this.$ajax({
          url: '/notice/getNotice.do',
          el: '.detail',
          query: {
            id: this.id
          }
        })
      },
    },
    mounted(){
      this.regulationsDetail()
    }
  }
</script>

<style lang='sass' scoped>
  .detail
    padding: 20px
    .title
      text-align: center
      font-size: $font-large
      color: $fc-def
    .prompt
      width: 200px
      margin: 20px auto
      font-size: $font-small
      color: $gray-shallow
</style>
